body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: 100%;
  background-position: bottom;
  background-color: #95e8f3 ;
  min-height: 100%;
}

#home{
  background-color: white;
  background-image: url('https://images.pexels.com/photos/1037996/pexels-photo-1037996.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  z-index: 2;
  height: 100vh;
}

.welcome-text{
  width: 80%;
  margin: 0 10%;
  height: 100%;
}

.welcome-text .welcome {
  margin: 0 auto;
  color: rgb(22, 12, 17);
}

#about{
  height: 100vh;
  background-color: rgb(249, 250, 252);
  background-image: url(https://images.pexels.com/photos/2008145/pexels-photo-2008145.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: multiply;   
}


form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5 {
  margin: 20px 0px;
}

footer ul {
  font-size: 30px;
  margin: 0 auto
}

footer ul li {
  display: inline;
  margin: 0 10px
}

.forum-list{
  background-color: whitesmoke;
  min-height: 100vh;
}

.create-forum {
  position: fixed;
  bottom: 10px;
}

/* .forum-text, .forum-button {
  position: fixed;
  bottom: 10px
} */

.forum-content{
  margin: 10px;
}

